class Mask {
  constructor(selector, regExpString = '+7 (___) ___-__-__') {
    this.elements = document.querySelectorAll(selector);
    this.regExpString = regExpString;
  }

  init() {
    this._listeners(this.elements);
  }

  _listeners(selector) {
    for (let i = 0; i < selector.length; i++) {
      const input = selector[i];
      input.addEventListener('input', this._mask.bind(this), false);
      input.addEventListener('focus', this._mask.bind(this), false);
      input.addEventListener('blur', this._mask.bind(this), false);
    }
  }

  _setCursorPosition(pos, elem) {
    elem.focus();
    if (elem.setSelectionRange) {
      elem.setSelectionRange(pos, pos);
    } else if (elem.createTextRange) {
      const range = elem.createTextRange();
      range.collapse(true);
      range.moveEnd('character', pos);
      range.moveStart('character', pos);
      range.select();
    }
  }

  _mask(event) {
    let matrix = this.regExpString,
      i = 0,
      def = matrix.replace(/\D/g, ''),
      val = event.target.value.replace(/\D/g, '');
    if (def.length >= val.length) {
      val = def;
    }
    event.target.value = matrix.replace(/./g, function(a) {
      return /[_\d]/.test(a) && i < val.length ? val.charAt(i++) : i >= val.length ? '' : a
    });
    if (event.type == 'blur') {
      if (event.target.value.length == 2) {
        event.target.value = '';
      }
    } else {
      this._setCursorPosition(event.target.value.length, event.target);
    }
  }
}

const utils = {
  cache: {
    fileInputs: new Map(),
  },
  //    js
  //       body:after
  mediaBreakpoint: {
    sm: 576,
    md: 768,
    lg: 1200,
    xl: 1366,
    xxl: 1920,
  },
  isMobile: {
    Android() {
      return navigator.userAgent.match(/Android/i);
    },
    BlackBerry() {
      return navigator.userAgent.match(/BlackBerry/i);
    },
    iOS() {
      return navigator.userAgent.match(/iPhone|iPad|iPod/i);
    },
    Opera() {
      return navigator.userAgent.match(/Opera Mini/i);
    },
    Windows() {
      return navigator.userAgent.match(/IEMobile/i);
    },
    any() {
      return (this.Android() || this.BlackBerry() || this.iOS() || this.Opera() || this.Windows());
    },
  },
  svgIcons() {
    const container = document.querySelector('[data-svg-path]');
    const path = container.getAttribute('data-svg-path');
    const xhr = new XMLHttpRequest();
    xhr.onload = () => {
      container.innerHTML = xhr.responseText;
    };
    xhr.open('get', path, true);
    xhr.send();
  },
  detectIE() {
    /**
     * detect IE
     * returns version of IE or false, if browser is not Internet Explorer
     */

     (function detectIE() {
       var ua = window.navigator.userAgent;

       var msie = ua.indexOf('MSIE ');
       if (msie > 0) {
        // IE 10 or older => return version number
        var ieV = parseInt(ua.substring(msie + 5, ua.indexOf('.', msie)), 10);
        document.querySelector('body').className += ' IE';
      }

      var trident = ua.indexOf('Trident/');
      if (trident > 0) {
        // IE 11 => return version number
        var rv = ua.indexOf('rv:');
        var ieV = parseInt(ua.substring(rv + 3, ua.indexOf('.', rv)), 10);
        document.querySelector('body').className += ' IE';
      }

      var edge = ua.indexOf('Edge/');
      if (edge > 0) {
        // IE 12 (aka Edge) => return version number
        var ieV = parseInt(ua.substring(edge + 5, ua.indexOf('.', edge)), 10);
        document.querySelector('body').className += ' IE';
      }

      // other browser
      return false;
    })();
  },
  truncateText(elements) {
    const cutText = () => {
      for (let i = 0; i < elements.length; i++) {
        const text = elements[i];
        const elemMaxHeight = parseInt(getComputedStyle(text).maxHeight, 10);
        const elemHeight = text.offsetHeight;
        const maxHeight = elemMaxHeight ? elemMaxHeight : elemHeight;

        shave(text, maxHeight);
      }
    };

    this.cache.cutTextListener = this.throttle(cutText, 100);

    cutText();

    window.addEventListener('resize', this.cache.cutTextListener);
  },
  throttle(callback, limit) {
    let wait = false;

    return () => {
      if (!wait) {
        callback.call();
        wait = true;
        setTimeout(() => {
          wait = false;
        }, limit);
      }
    };
  },
  getScreenSize() {
    let screenSize = window.getComputedStyle(document.querySelector('body'), ':after').getPropertyValue('content');
    screenSize = parseInt(screenSize.match(/\d+/), 10);
    return screenSize;
  },
  polyfills() {
    /**
     * polyfill for elem.closest
     */
    (function(ELEMENT) {
      ELEMENT.matches = ELEMENT.matches || ELEMENT.mozMatchesSelector || ELEMENT.msMatchesSelector || ELEMENT.oMatchesSelector || ELEMENT.webkitMatchesSelector;
      ELEMENT.closest = ELEMENT.closest || function closest(selector) {
        if (!this) return null;
        if (this.matches(selector)) return this;
        if (!this.parentElement) {
          return null;
        } else {
          return this.parentElement.closest(selector);
        } 
      };
    }(Element.prototype));

    /**
     * polyfill for elem.hasClass
     */
    Element.prototype.hasClass = function(className) {
      return this.className && new RegExp("(^|\\s)" + className + "(\\s|$)").test(this.className);
    };

    Number.isNaN = Number.isNaN || function(value) {
      return typeof value === 'number' && isNaN(value);
    };

    //   ECMA-262, 6- , 22.1.2.1
    // : https://people.mozilla.org/~jorendorff/es6-draft.html#sec-array.from
    if (!Array.from) {
      Array.from = (function() {
        var toStr = Object.prototype.toString;
        var isCallable = function(fn) {
          return typeof fn === 'function' || toStr.call(fn) === '[object Function]';
        };
        var toInteger = function (value) {
          var number = Number(value);
          if (isNaN(number)) { return 0; }
          if (number === 0 || !isFinite(number)) { return number; }
          return (number > 0 ? 1 : -1) * Math.floor(Math.abs(number));
        };
        var maxSafeInteger = Math.pow(2, 53) - 1;
        var toLength = function (value) {
          var len = toInteger(value);
          return Math.min(Math.max(len, 0), maxSafeInteger);
        };

        //  length  from  1.
        return function from(arrayLike/*, mapFn, thisArg */) {
          // 1.  C   this.
          var C = this;

          // 2.  items  ToObject(arrayLike).
          var items = Object(arrayLike);

          // 3. ReturnIfAbrupt(items).
          if (arrayLike == null) {
            throw new TypeError('Array.from requires an array-like object - not null or undefined');
          }

          // 4.  mapfn  undefined,  mapping  false.
          var mapFn = arguments.length > 1 ? arguments[1] : void undefined;
          var T;
          if (typeof mapFn !== 'undefined') {
            // 5. 
            // 5. a.   IsCallable(mapfn)  false,   TypeError.
            if (!isCallable(mapFn)) {
              throw new TypeError('Array.from: when provided, the second argument must be a function');
            }

            // 5. b.  thisArg ,  T  thisArg;   T  undefined.
            if (arguments.length > 2) {
              T = arguments[2];
            }
          }

          // 10.  lenValue  Get(items, "length").
          // 11.  len  ToLength(lenValue).
          var len = toLength(items.length);

          // 13.  IsConstructor(C)  true, 
          // 13. a.  A      [[Construct]]
          //      C   ,    len.
          // 14. a. ,  A  ArrayCreate(len).
          var A = isCallable(C) ? Object(new C(len)) : new Array(len);

          // 16.  k  0.
          var k = 0;
          // 17.  k < len,  ... (  a  h)
          var kValue;
          while (k < len) {
            kValue = items[k];
            if (mapFn) {
              A[k] = typeof T === 'undefined' ? mapFn(kValue, k) : mapFn.call(T, kValue, k);
            } else {
              A[k] = kValue;
            }
            k += 1;
          }
          // 18.  putStatus  Put(A, "length", len, true).
          A.length = len;
          // 20.  A.
          return A;
        };
      }());
    }
  },
};

const states = {
  TYPE_ACTIVE: 'active',
  ITEM_ANIMATED: ['animated-show', 'animated-hide'],
};
const elements = {
  FILTERS_TYPE: '[data-type-filter]',
  CURRENT_TYPE: `[data-type-filter].${states.TYPE_ACTIVE}`,
  ITEMS: '[data-type]',
};

class MyList {
  constructor(parent, id, optionsList = []) {
    this._id = id;
    this._optionsList = optionsList;
    this._parent = parent;
    this.list = [];

    this._initList();
  }

  get id() {
    return this._id;
  }

  _initListPlugin() {
    const options = {
      valueNames: [
        {data: ['type'],}
      ],
    };

    if (this._optionsList.length) {
      options.valueNames = options.valueNames.concat(...this._optionsList);
    }

    this.list = new List(this._id, options);
  }

  itemsToggleAnimated(boolean) {
    const list = this._parent.querySelector('.js-list');

    if (boolean) {
      list.classList.remove(states.ITEM_ANIMATED[1]);
      list.classList.add(states.ITEM_ANIMATED[0]);
    } else {
      list.classList.remove(states.ITEM_ANIMATED[0]);
      list.classList.add(states.ITEM_ANIMATED[1]);
    }
  }

  filterList() {
    const currentType = this._parent.querySelector(elements.CURRENT_TYPE).dataset.typeFilter;

    if (currentType !== 'all') {
      this.list.filter((item) => {
        return currentType === item.values().type;
      });
    } else {
      this.list.filter();
    }

    this.itemsToggleAnimated(true);
  }

  navItemRemoveActive() {
    const navItemsArr = Array.from(this._parent.querySelectorAll(elements.FILTERS_TYPE));

    navItemsArr.forEach((item) => {
      item.classList.remove(states.TYPE_ACTIVE);
    });
  }

  _initList() {
    if (!document.getElementById(this._id)) {
      console.error(`    id=${this._id}`);
      return;
    }
    this._initListPlugin();
    this.filterList();
  }
}

class CheckboxSelect {
  constructor(el) {
    this.el = el;
    this.toggle = this.el.querySelector('.js-select-checkbox-toggle');
    this.valueEl = this.el.querySelector('.js-select-checkbox-value');
    this.checkboxes = Array.from(this.el.querySelectorAll('input[type="checkbox"]'));
  }

  init() {
    const that = this;

    this.valueEl.textContent = this.checkboxes.filter((item) => item.checked).length;

    this.toggle.addEventListener('click', (e) => {
      this.el.classList.toggle('active');
    });

    this.checkboxes.forEach((item) => {
      item.addEventListener('change', this.changeCurrentValue.bind(this));
    });

    document.addEventListener('click', (e) => {
      if (!$(e.target).closest(that.el).length && that.el.classList.contains('active')) {
        that.el.classList.remove('active');
      }
    });
  }

  changeCurrentValue() {
    this.valueEl.textContent = this.checkboxes.filter((item) => item.checked).length;
  }

  documentClickHandler() {

  }
}

class FileInput {
  static createImage(file) {
    const img = document.createElement('img');

    img.src = URL.createObjectURL(file);
    img.alt = file.name;
    img.setAttribute('data-object-fit', 'cover');

    return img;
  }

  static createVideo(file) {
    const video = document.createElement('video');

    video.src = URL.createObjectURL(file);
    video.setAttribute('data-object-fit', 'cover');

    return video;
  }

  static createListItem(file, index) {
    const div = document.createElement('div');

    div.className = 'file-input__item responsive-img';
    div.setAttribute('data-index', index);

    if (/^image\//.test(file.type)) {
      div.append(FileInput.createImage(file));
    } else if (/^video\//.test(file.type)) {
      div.append(FileInput.createVideo(file));
    }

    return div;
  }

  constructor(el) {
    this._files = [];
    this.el = el;
    this.max = el.dataset.max;
    this.input = el.querySelector('.js-file-input-control');
    // this.imageList = el.querySelector('.js-file-input-list');
    this.dropdown = el.querySelector('.js-file-input-dropdown');
    this.dropdownValue = this.dropdown.querySelector('.js-file-input-dropdown-value');
    this.dropdownList = this.dropdown.querySelector('.js-file-input-dropdown-list');

    this.input.addEventListener('change', this.addFiles.bind(this));
  }

  getFiles() {
    return this._files;
  }

  addFiles() {
    const newFiles = this.input.files;

    for (const file of newFiles) {
      const listItem = FileInput.createListItem(file, this._files.length);
      this._files.push(file);

      if (this._files.length <= this.max) {
        this.dropdown.before(listItem);
      } else {
        this.updateDropdown(listItem);
      }
    }
  }

  updateDropdown(el) {
    this.dropdownValue.innerText = `+${this._files.length - this.max}`;
    this.dropdownList.append(el);

    if (this.dropdown.classList.contains('hidden')) {
      this.dropdown.classList.remove('hidden');
    }
  }
}

